body {
  background: url("../../public/images//all_bg.png");
  background-size: cover;
  width: 100%;
  height: 100%;
  background-color: #0F111D;
}

#Main_container {
  overflow: hidden;
  width: 100%;
  height: 100vh;
}
#Main_container .center {
  background: url("../../public/images/main_bg.jpg") no-repeat;
  background-size: cover;
  padding-top: 130px;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
#Main_container .center::before {
  content: "";
  background: url("../../public/images/mv_object.png") no-repeat;
  background-size: cover;
  width: 30%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  filter: brightness(-50%);
}
#Main_container .center .cont1 {
  height: 50%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  overflow: hidden;
  position: relative;
}
#Main_container .center .cont1 .top_img {
  height: 30%;
  display: flex;
  bottom: 0;
  overflow: hidden;
  justify-content: center;
  width: 100%;
  animation: fadeInUp 1s;
}
#Main_container .center .cont1 .top_img img {
  width: 7%;
  height: 100%;
}
#Main_container .center .cont1 .center_box {
  position: relative;
  left: 5%;
  display: flex;
  width: 30%;
  height: 50%;
  overflow: hidden;
}
#Main_container .center .cont1 .center_box .left_text {
  position: relative;
  width: 30%;
  height: 100%;
  left: 0;
  animation: imgon 2.5s;
}
#Main_container .center .cont1 .center_box .left_text img {
  width: 100%;
  height: 100%;
}
#Main_container .center .cont1 .center_box .right_text {
  margin-left: 30px;
  color: #fff;
  height: 100%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  position: relative;
}
#Main_container .center .cont1 .center_box .right_text .right_top {
  animation: fadeInRight 1s;
}
#Main_container .center .cont1 .center_box .right_text .right_top h1 {
  width: 200px;
  font-size: 22px;
  margin-bottom: 20px;
  font-weight: 700;
}
#Main_container .center .cont1 .center_box .right_text .right_top p {
  line-height: 20px;
  width: 250px;
}
#Main_container .center .cont1 .center_box .right_text .right_bottom {
  animation: fadeInRight 1.4s;
  position: absolute;
  bottom: 0;
}
#Main_container .center .cont1 .center_box .right_text .right_bottom p {
  width: 250px;
  line-height: 25px;
}
#Main_container .center::after {
  background: url("../../public/images/shadow.png") no-repeat;
  background-size: cover;
  content: "";
  width: 100%;
  height: 490px;
  position: absolute;
  bottom: -540px;
  left: 0px;
}

.bar_test {
  width: 100%;
  height: 50px;
  background-color: #c94165;
  display: flex;
  justify-content: center;
  font-size: 20px;
  line-height: 50px;
  overflow: hidden;
}
.bar_test .bar_menus {
  width: 600px;
  display: flex;
  justify-content: space-between;
}
.bar_test .bar_menus h2 {
  margin-left: 50px;
  font-family: "PyeongChangPeace-Bold";
}
@font-face {
  font-family: "PyeongChangPeace-Bold";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2206-02@1.0/PyeongChangPeace-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}
.bar_test .bar_menus marquee a {
  color: #000;
  font-size: 10pt;
  font-weight: 500;
}

.cont2 {
  width: 100%;
  background: url("../../public/images/section1_bg.png") no-repeat;
  background-size: cover;
  position: relative;
  z-index: 16;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.cont2 .box {
  transition: all 2s;
  opacity: 0;
  padding: 100px 0;
  flex-wrap: wrap;
  display: flex;
  justify-content: center;
  width: 100%;
}
.cont2 .box .top_box {
  width: 500px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  text-align: center;
}
.cont2 .box .top_box h2, .cont2 .box .top_box p {
  color: #fff;
  font-size: 12pt;
  line-height: 28px;
}
.cont2 .box .top_box .moon {
  width: 100px;
  height: 100px;
  animation: fadeInUp 0.8s;
}
.cont2 .box .top_box .sec_txt {
  width: 250px;
  height: 60px;
  animation: fadeInUp 0.8s;
  margin-bottom: 20px;
  transition-delay: 1.5s;
}
.cont2 .box .top_box p {
  transition-delay: 2s;
  animation: fadeInUp 0.8s;
}
.cont2 .box.on {
  opacity: 1;
}
.cont2 .box2 {
  display: flex;
  justify-content: center;
  width: 810px;
  height: 600px;
  flex-wrap: wrap;
}
.cont2 .box2 .video {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  box-shadow: 0 0 35px rgba(0, 0, 0, 0.3);
  border-radius: 15px;
}
.cont2 .box2 .video a {
  border-radius: 15px;
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-image: url("../../public/images//main-vd.jpg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
.cont2 .box2 .video a .top_box {
  background-color: #1f1f1f;
  color: #d6d6d6;
  border-radius: 25px;
  width: 200px;
  height: 50px;
  text-align: center;
  line-height: 50px;
  position: absolute;
  right: 20px;
  font-size: 10pt;
  top: 20px;
}
.cont2 .box2 .video a img {
  width: 100%;
  position: absolute;
  bottom: 20px;
  left: 0px;
  z-index: 16;
}
.cont2 .box2 .video a::before {
  content: "";
  background: url("../../public/images/ikon/big_tryi.png");
  width: 100px;
  height: 100px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.cont2 .box2 .video a::after {
  z-index: 15;
  content: "";
  position: absolute;
  display: block;
  width: 100%;
  height: 100px;
  bottom: 0;
  background: -webkit-linear-gradient(-90deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.9) 99%);
}
.cont2 .box2 .video_btn {
  margin-top: 35px;
  background-color: rgba(247, 1, 124, 0.721);
  width: 300px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  border-radius: 30px;
}
.cont2 .box2 .video_btn a {
  font-size: 15pt;
  color: #e2e2e2;
}
.cont2 .box3 {
  margin-top: 300px;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin-bottom: 250px;
}
.cont2 .box3 .top_text_box {
  animation: fadeInUp 1.5s;
  text-align: center;
  position: relative;
  margin-bottom: 50px;
}
.cont2 .box3 .top_text_box .title {
  font-size: 30pt;
  color: #fff;
  font-weight: 600;
  margin-bottom: 40px;
}
.cont2 .box3 .top_text_box p {
  font-size: 13pt;
  color: #fff;
}
.cont2 .box3 .top_text_box::after {
  background: url("../../public/images//ye.png") no-repeat;
  content: "";
  width: 180px;
  height: 180px;
  position: absolute;
  right: -170px;
  top: -50px;
  z-index: 10;
  filter: brightness(50%);
}
.cont2 .box3 .slide {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.cont2 .box3 .slide .slide_wrap {
  display: flex;
  justify-content: center;
  width: 100%;
  height: 880px;
  flex-wrap: wrap;
  position: relative;
  animation: imgon 2.5s;
}
.cont2 .box3 .slide .slide_wrap .top_box {
  height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: all 0.7s;
  position: absolute;
  text-align: center;
}
.cont2 .box3 .slide .slide_wrap .top_box .subTitle {
  height: 20px;
  font-size: 20pt;
  color: rgb(163, 163, 163);
  font-weight: bold;
  position: absolute;
  top: 100px;
  left: 50%;
  transform: translateX(-50%);
}
.cont2 .box3 .slide .slide_wrap .top_box .title {
  font-size: 120px;
  color: #fff;
  position: relative;
  top: 150px;
  z-index: 50;
  font-family: "PyeongChangPeace-Bold";
}
@font-face {
  font-family: "PyeongChangPeace-Bold";
  src: url("https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2206-02@1.0/PyeongChangPeace-Bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
}
.cont2 .box3 .slide .slide_wrap .top_box .titleTextTop {
  z-index: 50;
  position: relative;
  top: 180px;
  color: #fff;
  font-size: 15pt;
  margin-bottom: 20px;
}
.cont2 .box3 .slide .slide_wrap .top_box .titleTextBot {
  z-index: 50;
  position: relative;
  top: 180px;
  color: #fff;
  font-size: 15pt;
}
.cont2 .box3 .slide .slide_wrap .top_box .img_box {
  display: flex;
  justify-content: center;
  width: 100%;
  position: relative;
}
.cont2 .box3 .slide .slide_wrap .top_box .img_box img {
  position: relative;
  z-index: 45;
  width: 80%;
}
.cont2 .box3 .slide .slide_wrap .top_box .img_box::after {
  display: block;
  z-index: 44;
  content: "";
  background: url("../../public/images//obj_gr.png") no-repeat;
  width: 190px;
  height: 190px;
  position: absolute;
  right: 350px;
  top: -30px;
  background-size: cover;
  animation-duration: 0s;
  animation-timing-function: ease;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-fill-mode: none;
  animation-play-state: running;
  animation-name: spin;
  animation-duration: 15s;
}
.cont2 .box3 .slide .slide_wrap .top_box .img_box::before {
  content: "";
  background: url("../../public/images//obj_or.png") no-repeat;
  background-size: cover;
  width: 90px;
  height: 90px;
  position: absolute;
  left: 350px;
  top: 100px;
  z-index: 46;
  animation-duration: 0s;
  animation-timing-function: ease;
  animation-delay: 0s;
  animation-iteration-count: infinite;
  animation-direction: normal;
  animation-fill-mode: none;
  animation-play-state: running;
  animation-name: spin;
  animation-duration: 15s;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.cont2 .box3 .slide .slide_wrap .top_box.on {
  opacity: 1;
  visibility: visible;
}
.cont2 .box3 .slide .slide_btns {
  width: 100%;
  display: flex;
  position: relative;
}
.cont2 .box3 .slide .slide_btns > div {
  position: absolute;
  bottom: 60px;
  width: 200px;
  height: 50px;
  border-radius: 50%;
  line-height: 50px;
  text-align: center;
}
.cont2 .box3 .slide .slide_btns > div a {
  font-size: 30px;
  color: #fff;
}
.cont2 .box3 .slide .slide_btns .prev_btn {
  left: 10%;
}
.cont2 .box3 .slide .slide_btns .next_btn {
  right: 10%;
}
.cont2 .box3 .slide .slide_menus {
  display: flex;
  justify-content: center;
  background-color: transparent;
  width: 750px;
  height: 200px;
}
.cont2 .box3 .slide .slide_menus .menu {
  display: none;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
}
.cont2 .box3 .slide .slide_menus .menu li {
  width: 50%;
  margin-bottom: 10px;
  height: 40px;
}
.cont2 .box3 .slide .slide_menus .menu li a {
  height: 40px;
  text-align: left;
  color: #fff;
  font-size: 20px;
  position: relative;
  padding-left: 30px;
}
.cont2 .box3 .slide .slide_menus .menu li a::before {
  position: absolute;
  display: block;
  content: "";
  background: url("../../public/images//pink_ov_icon.png") no-repeat;
  width: 20px;
  height: 20px;
  background-size: cover;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
.cont2 .box3 .slide .slide_menus .menu.on {
  display: flex;
}
.cont2 .box4 {
  width: 100%;
}
.cont2 .box4 .titlebox {
  text-align: center;
}
.cont2 .box4 .titlebox h1 {
  color: #fff;
  font-size: 45px;
  margin-bottom: 40px;
  font-weight: bold;
}
.cont2 .box4 .titlebox p {
  color: #fff;
  font-size: 18px;
}
.cont2 .box4 .sub_btns {
  margin-top: 50px;
  display: flex;
  justify-content: center;
}
.cont2 .box4 .sub_btns > div {
  margin: 0 20px;
  width: 180px;
  height: 65px;
  line-height: 65px;
  border-radius: 45px;
  text-align: center;
  border: 1px solid #fff;
}
.cont2 .box4 .sub_btns > div a {
  color: #fff;
  font-size: 20px;
}
.cont2 .box4 .sub_btns > div:hover {
  background-color: #e15b7e;
  border: #e15b7e 1px solid;
  transition: all 0.5s;
}
.cont2 .box4 .slide_container {
  margin-top: 50px;
  width: 100%;
  overflow: hidden;
  position: relative;
  height: 620px;
}
.cont2 .box4 .slide_container .slide_carousel {
  justify-content: center;
  display: flex;
}
.cont2 .box4 .slide_container .slide_carousel > .item,
.cont2 .box4 .slide_container .slide_carousel2 > .item {
  width: 230px;
  margin-left: 20px;
  height: 230px;
}
.cont2 .box4 .slide_container .slide_carousel > .item img,
.cont2 .box4 .slide_container .slide_carousel2 > .item img {
  border-radius: 15px;
  width: 100%;
  height: 100%;
}
.cont2 .box4 .slide_container .slide_carousel2 {
  justify-content: center;
  display: flex;
  margin-top: 50px;
}

.box5 {
  opacity: 0;
  transition: all 3s;
  overflow: hidden;
  margin-top: 150px;
  margin-bottom: 50px;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.box5 .box5_title {
  text-align: center;
  width: 100%;
  height: 282px;
  overflow: hidden;
  animation: fadeInUp5 0.5s;
}
.box5 .box5_title img {
  position: relative;
  bottom: 0;
}
.box5 .lists {
  overflow: hidden;
  width: 1000px;
  margin-top: 100px;
  display: flex;
  justify-content: space-between;
  position: relative;
}
.box5 .lists li {
  width: 160px;
  height: 150px;
  border: 1px solid #3a3a3a;
  margin: 0 10px;
  border-radius: 15px;
  display: flex;
  align-items: center;
  position: relative;
  cursor: pointer;
}
.box5 .lists li a {
  margin-left: 15px;
  color: #fff;
  font-size: 20px;
  line-height: 25px;
}
.box5 .lists li img {
  position: absolute;
  width: 30px;
  height: 30px;
  right: 10px;
  bottom: 20px;
  font-size: 20px;
}
.box5 .lists li:hover {
  background-color: #e15b7e;
  border: #e15b7e 1px solid;
  transition: all 0.5s;
}

.box5.on {
  opacity: 1;
}

.footer_bg {
  position: relative;
  width: 100%;
  height: 150px;
  background: -webkit-linear-gradient(90deg, rgb(25, 39, 49), transparent);
}
.footer_bg::before {
  content: "";
  background: url("../../public/images//fooger_bg.png") no-repeat;
  width: 100%;
  height: 300px;
  background-size: cover;
  position: absolute;
  bottom: 0;
}

.footer {
  padding-top: 30px;
  background-color: #0a0e11;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
.footer > div {
  margin: 20px 0;
}
.footer .fot_top_logo {
  margin-top: 30px;
  width: 450px;
  height: 40px;
