#Sub2_container {
  width: 100%;
  padding-top: 100px;
}
#Sub2_container .center {
  display: flex;
  justify-content: center;
  width: 100%;
  flex-wrap: wrap;
}
#Sub2_container .center .title {
  width: 100%;
  text-align: center;
  font-size: 40px;
  font-weight: 700;
  padding: 20px 0;
  margin: 50px 0;
  color: #fff;
}
#Sub2_container .center .tbWrap .tb_title {
  width: 100%;
  font-size: 20px;
  font-weight: 600;
  margin: 10px 0;
  color: #fff;
}
#Sub2_container .center .tbWrap .tb {
  border-top: 1px solid #bd375b;
  width: 1200px;
  border-collapse: collapse;
  margin-bottom: 100px;
}
#Sub2_container .center .tbWrap .tb .top_tb {
  background-color: #f9f9f9;
}
#Sub2_container .center .tbWrap .tb .top_tb th {
  font-weight: 600;
  width: 50px;
  height: 30px;
  border-top: 1px solid #e4446f;
  font-size: 15px;
  color: #000;
  background-color: #fff;
}
#Sub2_container .center .tbWrap .tb td, #Sub2_container .center .tbWrap .tb th, #Sub2_container .center .tbWrap .tb tr {
  border: 1px solid rgb(207, 207, 207);
  width: 50px;
  height: 30px;
  font-size: 14px;
  font-weight: 400;
  color: #fff;
  line-height: 20px;
  background-color: #414141;
}
#Sub2_container .center .tbWrap .tb tr > th:nth-child(1) {
  background-color: #f9f9f9;
  font-weight: 500;
  color: rgb(73, 73, 73);
}

@media screen and (max-width: 1024px) {
  #Sub2_container .center .tbWrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
  }
  #Sub2_container .center .tbWrap .tb_title {
    margin-left: 20px;
  }
  #Sub2_container .center .tbWrap .tb {
    width: 95%;
  }
}
@media screen and (max-width: 768px) {
  #Sub2_container .center .tbWrap {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
  }
  #Sub2_container .center .tbWrap .tb_title {
    margin-left: 20px;
  }
  #Sub2_container .center .tbWrap .tb {
    width: 95%;
  }
}