#footer {
  position: relative;
  width: 100%;
  bottom: 0;
}
#footer .footer {
  padding-top: 30px;
  background-color: #e7e7e7;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}
#footer .footer > div {
  margin: 20px 0;
}
#footer .footer .fot_top_logo {
  margin-top: 30px;
  width: 450px;
  height: 40px;
  display: flex;
  justify-content: space-between;
}
#footer .footer .fot_top_logo img {
  width: 120px;
  height: 40px;
}
#footer .footer .fot_center_menu {
  width: 100%;
  display: flex;
  justify-content: center;
}
#footer .footer .fot_center_menu i {
  color: #555555;
  font-size: 25px;
  margin: 0 15px;
}
#footer .footer .fot_bot_copy p {
  text-align: center;
  color: #555555;
  font-size: 10pt;
  line-height: 40px;
}
#footer .footer_bg {
  position: relative;
  width: 100%;
  height: 150px;
  background: -webkit-linear-gradient(90deg, rgb(25, 39, 49), transparent);
}
#footer .footer_bg::before {
  content: "";
  background: url("../../public/images/fooger_bg.png") no-repeat;
  width: 100%;
  height: 300px;
  background-size: cover;
  position: absolute;
  bottom: 0;
}