#Sub3_container {
  width: 100%;
  padding-top: 100px;
}
#Sub3_container .center {
  display: flex;
  justify-content: center;
  width: 100%;
  flex-wrap: wrap;
  margin-bottom: 100px;
}
#Sub3_container .center .title {
  width: 100%;
  text-align: center;
  font-size: 40px;
  font-weight: 700;
  padding: 20px 0;
  margin: 50px 0;
  color: #fff;
}
#Sub3_container .center .imgbox {
  width: 1200px;
}
#Sub3_container .center .imgbox img {
  width: 100%;
}
#Sub3_container .center .btn {
  display: flex;
  justify-content: center;
  width: 100%;
  margin-top: 30px;
}
#Sub3_container .center .btn a {
  background-color: #d63c65;
  color: #fff;
  padding: 15px;
  border-radius: 5px;
  font-size: 15px;
}